AEA;EACE,WAAW;EACX,YAAY;EACZ,wBAAwB;EACxB,4BAA4B;EAC5B,2BAA2B;AAC7B;;AAEA;EACE,gBAAgB;EAChB,aAAa;EACb,sCAAsC;EACtC,mBAAmB;EACnB,wCAAwC;EACxC,0BAA0B;AAC5B;;AAEA;EACE,2BAA2B;AAC7B;;AAEA;EACE,aAAa;EACb,eAAe;EACf,UAAU;EACV,OAAO;EACP,MAAM;EACN,WAAW;EACX,YAAY;EACZ,cAAc;EACd,oCAAoC;EACpC,aAAa;EACb,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;EACE,yBAAyB;EACzB,aAAa;EACb,sBAAsB;EACtB,UAAU;EACV,gBAAgB;EAChB,mBAAmB;EACnB,wCAAwC;EACxC,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,kBAAkB;AACpB;;AAEA;EACE,WAAW;EACX,eAAe;EACf,iBAAiB;EACjB,eAAe;EACf,kBAAkB;EAClB,QAAQ;EACR,UAAU;AACZ;;AAEA;;EAEE,WAAW;EACX,qBAAqB;AACvB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;AACX;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,kBAAkB;EAClB,eAAe;EACf,yBAAyB;AAC3B;;AAEA;EACE,qBAAqB;EACrB,aAAa;AACf;;AAEA;EACE,aAAa;EACb,YAAY;EACZ,kBAAkB;EAClB,yBAAyB;EACzB,YAAY;EACZ,eAAe;EACf,eAAe;EACf;;kBAEgB;AAClB;;AAEA;EACE,yBAAyB;EACzB,sBAAsB;AACxB;;AAEA,kBAAkB;;AAElB,sBAAsB;AACtB;EACE;IACE,8BAA8B;EAChC;;EAEA;IACE,8BAA8B;EAChC;AACF;;AAEA;EACE;IACE,qCAAqC;EACvC;;EAEA,qDAAqD;EACrD;;IAEE,WAAW;EACb;AACF;;AAEA;EACE;IACE,0BAA0B;EAC5B;;EAEA;;IAEE,cAAc;EAChB;;EAEA;IACE,WAAW;EACb;;EAEA;IACE,WAAW;EACb;AACF","sourcesContent":[":root {\n  --page-bg-color: #e0eafc;\n  --header-bg-color: #1e3c72;\n  --title-color: #f0f8ff;\n  --widget-bg-color: #f0f8ff;\n  --widget-border-radius: 15px;\n  --widget-box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);\n  --primary-font-color: #333333;\n  --secondary-font-color: #666666;\n  --accent-color: #3498db;\n  --error-bg-color: #f8d7da;\n  --error-font-color: #721c24;\n  --font-family: 'Poppins', sans-serif;\n  --title-font-size: 24px;\n  --standard-font-size: 16px;\n  --info-font-size: 18px;\n  --padding: 15px;\n  --margin: 20px;\n  --transition-speed: 0.3s;\n}\n\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\nbody {\n  background-color: var(--page-bg-color);\n  font-family: var(--font-family);\n}\n\n#app {\n  min-height: 100vh;\n}\n\n.header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  background-color: var(--header-bg-color);\n  box-shadow: var(--widget-box-shadow);\n  padding: 5px;\n  position: sticky;\n  top: 0;\n  z-index: 1000;\n}\n\n.body {\n  display: grid;\n  grid-template-rows: repeat(2, min-content);\n  grid-template-columns: repeat(4, 1fr);\n  align-items: stretch;\n  justify-items: center;\n  padding: var(--margin);\n  gap: var(--margin);\n  max-width: 2560px;\n  margin: 0 auto;\n}\n\n.logo-container {\n  width: 60px;\n  height: 60px;\n  background-size: contain;\n  background-repeat: no-repeat;\n  background-position: center;\n}\n\n.title {\n  color: var(--title-color);\n  font-size: var(--title-font-size);\n  font-weight: 700;\n  letter-spacing: 2px;\n  margin: 0 20px;\n}\n\n.search-icon-container {\n  width: 30px;\n  height: 30px;\n  margin: 0 20px;\n  background-size: contain;\n  background-repeat: no-repeat;\n  background-position: center;\n  cursor: pointer;\n}\n\n.card {\n  background-color: var(--widget-bg-color);\n  border-radius: var(--widget-border-radius);\n  box-shadow: var(--widget-box-shadow);\n  min-width: 100%;\n  max-width: 600px;\n}\n\n.card-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding-inline: var(--padding);\n  padding-top: var(--padding);\n  border-bottom: 4px solid var(--page-bg-color);\n}\n\n.card-title {\n  color: var(--primary-font-color);\n  font-size: var(--title-font-size);\n  font-weight: 700;\n  margin-bottom: 10px;\n}\n\n.card-info-corner {\n  color: var(--secondary-font-color);\n  font-size: var(--info-font-size);\n  font-weight: 400;\n  margin-bottom: 10px;\n}\n\n.card-body {\n  color: var(--primary-font-color);\n  font-size: var(--standard-font-size);\n  font-weight: 400;\n  padding: var(--padding);\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  height: calc(100% - 57px); /* 57px is the height of the header */\n}\n\n.forecast-body .info-content {\n  font-style: italic;\n}\n\n.forecast-body .info-row {\n  flex-direction: column;\n  align-items: flex-start;\n  gap: 10px;\n}\n\n.forecast-body .info-content {\n  margin: 0;\n}\n\n.error-card {\n  background-color: var(--error-bg-color);\n  border: 1px solid var(--error-font-color);\n  grid-column: 1 / -1; /* span all columns */\n}\n\n.error-card * {\n  color: var(--error-font-color);\n}\n\n.error-body {\n  padding-top: var(--padding);\n  text-align: center;\n}\n\n.user-friendly-message {\n  font-weight: 600;\n}\n\n.detailed-message {\n  padding-top: var(--padding);\n}\n\n.current-weather-hero {\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n  grid-template-rows: min-content min-content min-content;\n  grid-template-areas:\n    'address address'\n    'icon       temp'\n    'conditions conditions';\n  justify-content: center;\n  align-items: center;\n  gap: var(--padding);\n}\n\n.address {\n  grid-area: address;\n  font-size: var(--info-font-size);\n  font-weight: 600;\n}\n\n.weather-icon {\n  grid-area: icon;\n  align-self: center;\n  justify-self: center;\n  width: 60px;\n  height: 60px;\n}\n\n.temp-container {\n  grid-area: temp;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.current-temp {\n  font-size: 48px;\n  font-weight: 700;\n}\n\n.temp-unit {\n  align-self: flex-end;\n  margin-left: -16px;\n  margin-bottom: 4px;\n  font-size: 18px;\n  font-weight: 700;\n  color: var(--secondary-font-color);\n}\n\n.conditions {\n  grid-area: conditions;\n  align-self: center;\n  justify-self: center;\n  font-size: var(--info-font-size);\n  font-weight: 600;\n}\n\n.separator {\n  padding-bottom: var(--padding);\n  border-bottom: 4px solid var(--page-bg-color);\n}\n\n.info-row {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding-top: var(--padding);\n}\n\n.info-icon {\n  width: 50px;\n  height: auto;\n  margin-block: -10px;\n  padding-right: 10px;\n  margin-right: 10px;\n  background-size: contain;\n  background-repeat: no-repeat;\n  background-position: center;\n  border-right: 4px solid var(--page-bg-color);\n}\n\n.info-title {\n  font-size: var(--info-font-size);\n  font-weight: 600;\n}\n\n.info-content {\n  font-size: var(--info-font-size);\n  font-weight: 400;\n  margin-left: auto;\n}\n\n#hours-preview {\n  max-width: 100%;\n  grid-column: 1 / -1;\n}\n\n#hours-preview .card-body {\n  padding-block: 0;\n}\n\n.hourly-weather-body {\n  display: flex;\n  overflow-x: auto;\n  scroll-snap-type: x mandatory;\n  gap: 20px;\n  padding: var(--padding);\n  scroll-padding: 4px;\n  scroll-snap-align: start;\n  -webkit-overflow-scrolling: touch; /* Für sanftes Scrollen auf iOS */\n  scrollbar-width: thin; /* Dünne Scrollbar */\n  scrollbar-color: rgba(0, 0, 0, 0.5) transparent; /* Farbe des Scrollbar-Daumen und Hintergrund */\n}\n\n.hourly-weather-body:last-child {\n  padding-right: 4px;\n}\n\n/* Webkit-basierte Browser */\n.hourly-weather-body::-webkit-scrollbar {\n  height: 8px; /* Höhe der horizontalen Scrollbar */\n}\n\n.hourly-weather-body::-webkit-scrollbar-thumb {\n  background-color: rgba(0, 0, 0, 0.5); /* Farbe des Scrollbar-Daumen */\n  border-radius: 10px; /* Abgerundete Ecken */\n}\n\n.hour-column {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n  align-items: center;\n  gap: calc(var(--padding) / 2);\n  scroll-snap-align: start;\n  max-width: min-content;\n}\n\n.hour-time {\n  font-size: var(--info-font-size);\n}\n\n.hour-temp {\n  font-size: var(--info-font-size);\n  font-weight: 400;\n  width: min-content;\n}\n\n.hour-icon {\n  width: 40px;\n  height: 40px;\n  background-size: contain;\n  background-repeat: no-repeat;\n  background-position: center;\n}\n\n.hour-conditions {\n  font-size: 14px;\n  font-weight: 400;\n  min-height: 50px;\n  text-align: center;\n  display: flex;\n  align-items: flex-end;\n  justify-content: center;\n}\n\n.hour-precip-container {\n  display: flex;\n  align-items: center;\n  justify-content: flex-end;\n  width: 58px;\n}\n\n.hour-precip-value {\n  font-size: 14px;\n  font-weight: 400;\n}\n\n#daily-weather {\n  max-width: 100%;\n  grid-column: 1 / -1;\n}\n\n#daily-weather .card-body {\n  padding-block: 0;\n}\n\n.daily-weather-body {\n  display: flex;\n  overflow-x: auto;\n  scroll-snap-type: x mandatory;\n  gap: 20px;\n  padding: var(--padding);\n  scroll-padding: 4px;\n  scroll-snap-align: start;\n  -webkit-overflow-scrolling: touch; /* Für sanftes Scrollen auf iOS */\n  scrollbar-width: thin; /* Dünne Scrollbar */\n  scrollbar-color: rgba(0, 0, 0, 0.5) transparent; /* Farbe des Scrollbar-Daumen und Hintergrund */\n}\n\n.daily-weather-body:first-child {\n  padding-left: 4px;\n}\n\n.daily-weather-body:last-child {\n  padding-right: 4px;\n}\n\n.daily-weather-body::-webkit-scrollbar {\n  height: 8px; /* Höhe der horizontalen Scrollbar */\n}\n\n.daily-weather-body::-webkit-scrollbar-thumb {\n  background-color: rgba(0, 0, 0, 0.5); /* Farbe des Scrollbar-Daumen */\n  border-radius: 10px; /* Abgerundete Ecken */\n}\n\n.day-column {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n  align-items: center;\n  gap: calc(var(--padding) / 2);\n  scroll-snap-align: start;\n}\n\n.day-date {\n  font-size: var(--info-font-size);\n  text-wrap: nowrap;\n}\n\n.day-name {\n  font-size: var(--info-font-size);\n  font-weight: 600;\n}\n\n.day-icon {\n  width: 40px;\n  height: 40px;\n  background-size: contain;\n  background-repeat: no-repeat;\n  background-position: center;\n}\n\n.day-temp-container {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  margin-top: 5px;\n}\n\n.day-temp {\n  font-size: var(--info-font-size);\n  font-weight: 400;\n  width: min-content;\n}\n\n.day-min-max-temp {\n  font-size: 14px;\n  font-weight: 400;\n}\n\n.day-precip-container {\n  display: flex;\n  align-items: center;\n  justify-content: flex-end;\n  width: 58px;\n  margin-top: 5px;\n}\n\n.day-precip-value {\n  font-size: 14px;\n  font-weight: 400;\n}\n\n.day-precip-icon {\n  width: 20px;\n  height: 20px;\n  background-size: contain;\n  background-repeat: no-repeat;\n  background-position: center;\n}\n\n.highlighting-card {\n  max-width: 200px;\n  padding: 20px;\n  background-color: var(--page-bg-color);\n  border-radius: 10px;\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);\n  transition: transform 0.3s;\n}\n\n.highlighting-card:hover {\n  transform: translateY(-5px);\n}\n\n.modal {\n  display: none;\n  position: fixed;\n  z-index: 1;\n  left: 0;\n  top: 0;\n  width: 100%;\n  height: 100%;\n  overflow: auto;\n  background-color: rgba(0, 0, 0, 0.5);\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n.modal-content {\n  background-color: #fefefe;\n  padding: 20px;\n  border: 1px solid #888;\n  width: 80%;\n  max-width: 500px;\n  border-radius: 10px;\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n  display: flex;\n  flex-direction: column;\n  gap: 15px;\n  position: relative;\n}\n\n.close-button {\n  color: #aaa;\n  font-size: 28px;\n  font-weight: bold;\n  cursor: pointer;\n  position: absolute;\n  top: 2px;\n  right: 5px;\n}\n\n.close-button:hover,\n.close-button:focus {\n  color: #000;\n  text-decoration: none;\n}\n\n.modal-form {\n  display: flex;\n  flex-direction: column;\n  gap: 15px;\n}\n\n.modal-input {\n  padding: 12px;\n  border: 1px solid #ddd;\n  border-radius: 8px;\n  font-size: 16px;\n  background-color: #f9f9f9;\n}\n\n.modal-input:focus {\n  border-color: #007bff;\n  outline: none;\n}\n\n.modal-submit-button {\n  padding: 12px;\n  border: none;\n  border-radius: 8px;\n  background-color: #007bff;\n  color: white;\n  font-size: 16px;\n  cursor: pointer;\n  transition:\n    background-color 0.3s,\n    transform 0.2s;\n}\n\n.modal-submit-button:hover {\n  background-color: #0056b3;\n  transform: scale(1.05);\n}\n\n/* MEDIA QUERIES */\n\n/* Ultrawide screens */\n@media (min-width: 1600px) {\n  .hourly-weather-body {\n    justify-content: space-between;\n  }\n\n  .daily-weather-body {\n    justify-content: space-between;\n  }\